import styled from '@emotion/styled';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';

import { useLogin } from '@/api/hooks/auth';
import KAKAO_LOGO from '@/assets/kakao_logo.svg';
import { Button } from '@/components/common/Button';
import { UnderlineTextField } from '@/components/common/Form/Input/UnderlineTextField';
import { Spacing } from '@/components/common/layouts/Spacing';
import { Footer } from '@/components/features/Layout/Footer'; // Footer import
import { Header } from '@/components/features/Layout/Header'; // Header import
import { breakpoints } from '@/styles/variants';
import { authSessionStorage } from '@/utils/storage';

type LoginResponse = {
  accessToken: string;
  //     
};

export const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();
  const { mutate: login } = useLogin();

  const handleLogin = () => {
    if (!email || !password) {
      alert('  .');
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      alert('   .');
      return;
    }

    login(
      { email, password },
      {
        onSuccess: (data: LoginResponse) => {
          authSessionStorage.set(data.accessToken);
          alert(' ');
          window.location.replace(`${window.location.origin}/`);
        },
        onError: () => {
          alert('   .');
        },
      },
    );
  };

  const handleSignup = () => {
    navigate('/signup');
  };

  return (
    <Wrapper>
      <Header />
      <Content>
        <Logo src={KAKAO_LOGO} alt=" CI" />
        <FormWrapper>
          <UnderlineTextField
            type="email"
            placeholder=""
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <Spacing />
          <UnderlineTextField
            type="password"
            placeholder=""
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <Spacing height={{ initial: 40, sm: 60 }} />
          <Button onClick={handleLogin}></Button>
          <Spacing height={20} />
          <Button theme="outline" onClick={handleSignup}>
            
          </Button>
        </FormWrapper>
      </Content>
      <Footer />
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  min-height: 100vh;
`;

const Content = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const Logo = styled.img`
  width: 88px;
  color: #333;
`;

const FormWrapper = styled.article`
  width: 100%;
  max-width: 580px;
  padding: 16px;

  @media screen and (min-width: ${breakpoints.sm}) {
    border: 1px solid rgba(0, 0, 0, 0.12);
    padding: 60px 52px;
  }
`;
